import { Component, effect, Inject, OnDestroy, OnInit } from '@angular/core'
import { CommonModule, DOCUMENT } from '@angular/common'
import { FormsModule } from '@angular/forms'
import {
  IonContent,
  IonSearchbar,
  IonIcon,
  IonList,
  IonItem,
  IonLabel,
  IonSpinner,
  IonButton,
  IonRow,
  IonCol,
} from '@ionic/angular/standalone'
import {
  ListVehicule200ResponseAllOfDataInner,
  VehiculesService,
} from 'src/api-cyclist'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { addIcons } from 'ionicons'
import {
  searchOutline,
  checkmarkOutline,
  batteryFullOutline,
  alertCircleOutline,
  closeOutline,
  locationOutline,
  locateOutline,
} from 'ionicons/icons'
import { environment } from 'src/environments/environment'
import { Subscription } from 'rxjs'
import { ToastService, ToastType } from 'src/app/services/utils/toast.service'
import { RentingService } from 'src/app/services/api-wrappers/renting.service'
import { NearestVehiclesComponent } from 'src/app/components/drawers/nearest-vehicles/nearest-vehicles.component'
import {
  GeolocationService,
  GeolocationState,
} from 'src/app/services/utils/geolocation.service'
import {
  AppState,
  AppstateService,
} from 'src/app/services/internal/appstate.service'
import { ActivatedRoute, Router, RouterModule } from '@angular/router'

import { NativeAudioService } from 'src/app/services/utils/native-audio.service'
import { AuthService, AuthState } from 'src/app/services/external/auth.service'
import {
  AccountService,
  cyclistState,
} from 'src/app/services/api-wrappers/account.service'
import { LaunchService } from 'src/app/services/internal/launch.service'
import { ErrorHandlerService } from 'src/app/services/utils/error-handler.service'
declare var LeaderLine: any

export type BikeNumberSearchState =
  | {
      state: 'initial'
    }
  | {
      state: 'searching'
      searchInput: string
    }
  | {
      state: 'found'
      searchInput: string
      vehicle: ListVehicule200ResponseAllOfDataInner
    }
  | {
      state: 'notFound'
    }
  | {
      state: 'renting'
      searchInput: string
      vehicle: ListVehicule200ResponseAllOfDataInner
    }
  | {
      state: 'rentingError'
      error: any
    }

@Component({
  selector: 'app-bike-number',
  templateUrl: './bike-number.page.html',
  styleUrls: ['./bike-number.page.scss'],
  imports: [
    IonContent,
    IonSearchbar,
    IonButton,
    IonIcon,
    IonList,
    IonItem,
    IonLabel,
    IonRow,
    IonCol,
    IonSpinner,
    HeaderComponent,
    CommonModule,
    FormsModule,
    TranslateModule,
    NearestVehiclesComponent,
    RouterModule,
  ],
})
export class BikeNumberPage implements OnDestroy, OnInit {
  pageState: { state: 'loading_started' } | { state: 'loading_finished' } = {
    state: 'loading_started',
  }
  bikeNumberSearchState: BikeNumberSearchState = { state: 'initial' }
  private lastRequest: Subscription | null = null
  geolocState: GeolocationState | null = null
  appState: AppState | null = null
  private auth: AuthState = { state: 'SignedOut' }
  private cyclist: cyclistState = {
    state: 'unknown',
    blocked_reasons: ['Unknown account state'],
  }
  private leaderLine?: any

  constructor(
    @Inject(DOCUMENT) private document: Document,
    private vehicleService: VehiculesService,
    private rentingService: RentingService,
    private toastService: ToastService,
    private geolocationService: GeolocationService,
    private appStateService: AppstateService,
    private nativeAudioService: NativeAudioService,
    private router: Router,
    private route: ActivatedRoute,
    private authService: AuthService,
    private accountService: AccountService,
    private launchService: LaunchService,
    private toastrService: ToastService,
    private translate: TranslateService,
    private errorHandlerService: ErrorHandlerService
  ) {
    addIcons({
      searchOutline,
      alertCircleOutline,
      checkmarkOutline,
      batteryFullOutline,
      closeOutline,
      locateOutline,
      locationOutline,
    })
    this.geolocationService.geolocationState.subscribe((state) => {
      this.geolocState = state
    })

    this.appStateService.appState.subscribe((state) => {
      this.appState = state
    })
    // Listen to auth state changes
    effect(() => {
      this.auth = this.authService.authState()
    })

    // Listen to cyclist state changes
    effect(() => {
      this.cyclist = this.accountService.cyclistState()
    })
  }

  ionViewWillEnter() {
    this.performBackgroundLaunch()
  }

  ngOnInit() {
    // Préchargement du son "Dring"
    this.nativeAudioService.preloadAsset(
      'bikebell',
      'assets/audio/bikebell.mp3',
      1,
      0.8
    )
  }

  ionViewDidEnter() {
    const el = this.document.getElementById(
      'bike-number'
    ) as unknown as HTMLIonSearchbarElement
    el?.setFocus()

    if (
      this.geolocState?.state !== 'live_position' &&
      this.geolocState?.state !== 'unique_position'
    ) {
      this.nativeAudioService.play('bikebell')
    }
  }

  canStartRenting() {
    return (
      this.bikeNumberSearchState.state === 'found' &&
      this.bikeNumberSearchState.vehicle.rentable === true
    )
  }

  handleInput(event: CustomEvent) {
    // Annuler la dernière requête si elle existe
    if (this.lastRequest) {
      this.lastRequest.unsubscribe()
      this.lastRequest = null
    }

    const value = event.detail.value?.trim()

    if (!value) {
      this.bikeNumberSearchState = { state: 'initial' }
      return
    }

    this.bikeNumberSearchState = {
      state: 'searching',
      searchInput: value,
    }

    // Stocker la nouvelle requête
    this.lastRequest = this.vehicleService
      .retrieveVehicule({
        program: environment.program,
        id: value,
      })
      .subscribe({
        next: (vehicle) => {
          if (this.bikeNumberSearchState.state === 'searching') {
            this.bikeNumberSearchState = {
              state: 'found',
              searchInput: value,
              vehicle,
            }
          }
        },
        error: (error) => {
          if (error.status === 404) {
            this.bikeNumberSearchState = {
              state: 'notFound',
            }
          } else {
            this.toastService.createWithJustMessage(
              error.message,
              ToastType.Error
            )
          }
        },
      })
  }

  handleFocus() {
    // A laisser car sinon il n'est pas possible de focus à l'arrivée sur la page
  }

  startRenting() {
    if (this.bikeNumberSearchState.state === 'found') {
      try {
        this.bikeNumberSearchState = {
          state: 'renting',
          searchInput: this.bikeNumberSearchState.searchInput,
          vehicle: this.bikeNumberSearchState.vehicle,
        }
        this.rentingService.startRenting(this.bikeNumberSearchState.vehicle!)
      } catch (error) {
        this.bikeNumberSearchState = {
          state: 'rentingError',
          error,
        }
      }
    }
  }

  ngOnDestroy() {
    if (this.lastRequest) {
      this.lastRequest.unsubscribe()
    }
  }

  async requestLocation() {
    if (
      this.geolocState?.state === 'denied' ||
      this.geolocState?.state === 'error'
    ) {
      this.geolocationService.presentDeniedPositionAlert()
    } else {
      this.geolocationService.askForPermission()
    }
  }

  preventPaste(event: ClipboardEvent) {
    event.preventDefault()
    return false
  }

  onKeyPress(event: KeyboardEvent) {
    const pattern = /[0-9]/
    const inputChar = String.fromCharCode(event.charCode)

    if (!pattern.test(inputChar)) {
      event.preventDefault()
    }
  }

  /** *************************************************************** */
  /** ********** Ces méthodes servent principalement dans le cas ou l'utilisateur arrive par universal link avec une app fermée, si l'app est déjà lancé les vérifications se feront instantanément ********** */
  /** *************************************************************** */

  private async performBackgroundLaunch(): Promise<void> {
    this.pageState = { state: 'loading_started' }
    try {
      await this.launchService.loadEssentialData({
        skipIfDataExists: true,
        forceRefreshStations: false,
      })
      await this.waitForAuthentication()
    } catch (error) {
      this.errorHandlerService.handleError(error)
    }
  }

  private async waitForAuthentication(): Promise<void> {
    //Si utilisateur = déco, on attends 2 secondes le temps que l'auth se fasse
    if (this.auth.state === 'SignedOut') {
      await new Promise((resolve) => setTimeout(resolve, 2000)).then(() => {
        //Si il est toujours déco, on affiche l'erreur
        if (this.auth.state === 'SignedOut') {
          this.toastrService.createWithJustMessage(
            this.translate.instant('qr_code.error.authentication_required'),
            ToastType.Info
          )
          this.router.navigate(['/landing'], { replaceUrl: true })
        } else {
          //Sinon on vérifie que le cyclist soit valide
          this.waitForCyclistState()
        }
      })
    } // sinon on check directement le cycliste
    else this.waitForCyclistState()
  }

  private async waitForCyclistState(): Promise<void> {
    //Si le cyclist a une blocked_reason, on attends de voir si c'est pas juste un problème de chargement du cycliste (unknown account state)
    if (this.cyclist.blocked_reasons.length > 0) {
      const maxRetries = 25
      const checkInterval = 200
      for (let i = 0; i < maxRetries; i++) {
        // Si "Unknown account state" n'est plus présent, on peut arrêter la boucle
        if (!this.cyclist.blocked_reasons.includes('Unknown account state')) {
          break
        }
        await new Promise((resolve) => setTimeout(resolve, checkInterval))
      }
      // Si a l'issue il est encore bloqué, on affiche l'erreur
      if (this.cyclist.blocked_reasons.length > 0) {
        this.errorHandlerService.handleError({
          error: { decline_code: 'account_blocked' },
        })
      } else {
        this.handleQrCodeRedirection()
      }
    } else {
      this.handleQrCodeRedirection()
    }
  }

  private handleQrCodeRedirection(): void {
    this.route.queryParams.subscribe(async (params) => {
      if (params['qrcode'] && params['qrcode'] !== undefined) {
        this.router.navigate(['/renting/qr-code'], {
          queryParams: { qrcode: params['qrcode'] },
        })
      } else {
        this.pageState = { state: 'loading_finished' }
      }
    })
  }
}
